// btn
.btn {
  // @include font($black-1A1, "RedHatDisplay-Bold", 1.6rem, bold, normal);
  @include font($black-1A1, "RedHatDisplay-Medium", 1.4rem, 500, 24px);
  letter-spacing: -0.2px;
  border-radius: 4px;
  // border: 1px solid $primary;
  padding: 1.1rem 6rem;
  cursor: pointer;
  &:hover,
  &:active,
  &:focus,
  &:active:focus {
    outline: none;
    box-shadow: none;
  }

  &:not(:disabled),
  &:not(.disabled) {
    cursor: auto;
  }

  &.clear-btn {
    background-color: transparent;
    padding: 0;
    border: 0;
    &.btn-primary:not(:disabled):not(:disabled):active,
    &.btn-primary:not(:disabled):not(:disabled):active:focus,
    &:hover,
    &:active,
    &:focus,
    &:active:focus {
      border: 0;
      background-color: transparent;
      color: $primary;
      outline: none;
      box-shadow: none;
    }
  }
}

.black-btn {
  background-color: $primary;
  color: $secondary;
  border: 1px solid $primary;
  &.btn-primary:not(:disabled):not(:disabled):active,
  &.btn-primary:not(:disabled):not(:disabled):active:focus,
  &:hover,
  &:active,
  &:focus,
  &:active:focus {
    box-shadow: none;
    border: 1px solid $primary;
    background-color: $primary;
    color: $secondary;
  }
}
.inactive-btn {
  background-color: $grey-E7E;
  color: $grey-9E9;
  border: 1px solid $grey-E7E;
  cursor: not-allowed;
  &:hover,
  &:active,
  &:focus,
  &:active:focus {
    border: 1px solid $grey-E7E;
    background-color: $grey-E7E;
    color: $grey-9E9;
  }
}
.secondary-btn {
  background: transparent;
  color: $primary;
  border: 1px solid $primary;
  &.btn-primary:not(:disabled):not(:disabled):active,
  &.btn-primary:not(:disabled):not(:disabled):active:focus,
  &:hover,
  &:active,
  &:focus,
  &:active:focus {
    box-shadow: none;
    background: transparent;
    color: $primary;
    border: 1px solid $primary;
  }
}
.inactive-state {
  cursor: not-allowed !important;
  opacity: 0.6;
}

